
.shaking {
  animation: shake 0.3s infinite;
}
@-moz-keyframes shake {
0% {
    transform: rotate(0deg);
}
25% {
    transform: rotate(20deg);
}
50% {
    transform: rotate(0deg);
}
75% {
    transform: rotate(-20deg);
}
100% {
    transform: rotate(0deg);
}
}
@-webkit-keyframes shake {
0% {
    transform: rotate(0deg);
}
25% {
    transform: rotate(20deg);
}
50% {
    transform: rotate(0deg);
}
75% {
    transform: rotate(-20deg);
}
100% {
    transform: rotate(0deg);
}
}
@-o-keyframes shake {
0% {
    transform: rotate(0deg);
}
25% {
    transform: rotate(20deg);
}
50% {
    transform: rotate(0deg);
}
75% {
    transform: rotate(-20deg);
}
100% {
    transform: rotate(0deg);
}
}
@keyframes shake {
0% {
    transform: rotate(0deg);
}
25% {
    transform: rotate(20deg);
}
50% {
    transform: rotate(0deg);
}
75% {
    transform: rotate(-20deg);
}
100% {
    transform: rotate(0deg);
}
}


